@import url(https://fonts.googleapis.com/css2?family=Bungee+Inline&display=swap);
@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-Color: "black"
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container1{
    display: flex;
    padding: 1rem;
    max-height: 90vh;
    background-color: black;
    color: gray;

}
.camera{
  display: flex;
  padding: 0 1rem 0 1rem;
}
/* remove link styles */
a{
    text-decoration: none;
    color: gray;
}
.container{
    margin: 0;
    box-sizing: border-box;
    min-height: 100vh;
    min-width: 100vw;
    background-color: rgb(0, 0, 0);
    color: rgba(201,46,124,255);
    overflow: hidden;
    font-family: Bebas Neue;
    font-size: 3rem;
    justify-items: center;
    align-items: center;
    display: grid;
    grid-template-rows: 30vh 50vh 20vh;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-areas: "a l f"
                         "b c d"
                         "e e e";

}

.overlay{
    position:absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #00000099;
    color: gray;
    height: 100vh;
    width: 100vw;
    z-index: 1000;
    font-size: 4rem;
    text-align: center;
}
.slider{
    -webkit-animation: infinite 2s slide-up;
            animation: infinite 2s slide-up;
    cursor: pointer;
}
.unlock-text{
    text-align: center;
    cursor: pointer;
}


.logo{
    grid-area: l;
    color:rgb(32, 102, 252);
    padding: 1rem;
    text-align: center;
    /* border: 3px solid rgb(32, 102, 252); */
    /* border-radius: 1rem; */
}

.lock{
    border-radius: 10rem;
    height: 5rem;
    width: 5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    color: rgb(0, 230, 38);
    border :4px solid rgb(0, 230, 38);
    padding :10px;
 
    grid-area: l;
}



.car{
  
   grid-area: c;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 10rem;
    
    width: 10rem;
}

/* PUC Meter */
.meter-1{
    border-radius: 20rem;
    height: 20rem;
    width: 20rem;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
    color: rgb(0, 230, 38);
    border :4px solid rgb(0, 230, 38);
    
    background-color: rgb(14, 14, 14);
    text-align: center;
    grid-area: b;
   
}

#puc{
    height: 10rem;
    width: 20rem;
}

.meter-2{
    border-radius: 20rem;
    height: 20rem;
    width: 20rem;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
    color: rgb(0, 230, 38);
    border :4px solid rgb(0, 230, 38);
    background-color: rgb(14, 14, 14);
    padding :10px;
    text-align: center;
    grid-area: d;
    
}
#ic{
    height: 10rem;
    width: 20rem;
}
.infobar{
    grid-area: e;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    font-family: 'JetBrains Mono';
    font-size: 1.1rem;
    border: 2px solid rgb(0, 230, 38);
    color: rgb(32, 102, 252);
    border-color: rgb(18, 148, 253);    
    align-self: flex-end;
    border-radius:1rem 1rem 0 0 ;
    border-bottom: 0px;
    padding: 1rem;

}

.disco-text{
    -webkit-animation: infinite 3s unlock;
            animation: infinite 3s unlock;
}


#bugatti{
    transform: rotate(-90deg) scale(0.5);
    -webkit-animation: infinite 3s my-bugatti;
            animation: infinite 3s my-bugatti;
    transition:transform 3ms ease--out ;
}


@-webkit-keyframes unlock {
    0%{
        color: rgb(32, 102, 252);
        border-color: rgb(18, 148, 253);
    }
    50%{
        color: rgba(201,46,124,255);
        border-color: rgba(201,46,124,255);
    }
    100%{
        color: rgb(32, 102, 252);
        border-color: rgb(18, 148, 253);
    }
}


@keyframes unlock {
    0%{
        color: rgb(32, 102, 252);
        border-color: rgb(18, 148, 253);
    }
    50%{
        color: rgba(201,46,124,255);
        border-color: rgba(201,46,124,255);
    }
    100%{
        color: rgb(32, 102, 252);
        border-color: rgb(18, 148, 253);
    }
}


@-webkit-keyframes my-bugatti {
    0%{
        -webkit-filter: hue-rotate(0deg) ;
                filter: hue-rotate(0deg) ;
    }
  
    50%{
        -webkit-filter: hue-rotate(90deg);
                filter: hue-rotate(90deg);
    }
   
    100%{
        -webkit-filter: hue-rotate(0deg);
                filter: hue-rotate(0deg);
    }

}


@keyframes my-bugatti {
    0%{
        -webkit-filter: hue-rotate(0deg) ;
                filter: hue-rotate(0deg) ;
    }
  
    50%{
        -webkit-filter: hue-rotate(90deg);
                filter: hue-rotate(90deg);
    }
   
    100%{
        -webkit-filter: hue-rotate(0deg);
                filter: hue-rotate(0deg);
    }

}

@-webkit-keyframes slide-up{
    0%{
        transform: translateY(2rem);
        opacity: 0.7;
    }
    100%{        transform: translateY(-2rem);
        opacity: 1;

    }
}

@keyframes slide-up{
    0%{
        transform: translateY(2rem);
        opacity: 0.7;
    }
    100%{        transform: translateY(-2rem);
        opacity: 1;

    }
}
